.topNav {
  padding: 10px 10vw;
  position: fixed;
  display: grid;
  grid-template-columns: 3vw 45vw 35vw;
  align-items: center;
  background-color: black;
  vertical-align: middle;
}
.logo {
  margin: 0;
  height: 100%;
  width: 100%;
}
.topButtonsWarper {
  vertical-align: middle;
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.topButtons {
  font-weight: bold;
  font-family: "Calistoga", cursive;
  text-decoration-color: grey;
  font-size: 1.2em;
  vertical-align: middle;
}
