#header-experience {
  padding: 10px;
}
.project-name {
  font-size: 1.5em;
}
.experienceCardHeader {
  padding: 0px;
  background-color: blue;
  border: 1px solid blue;
}
.experienceCard {
  padding: 4vh;
  border: 1px solid white;
  border-radius: 10px;
}
.experienceCardBody {
  margin-bottom: 2vh;
  border-bottom: 1px solid black;
}

.experience {
  padding: 0px 10vw;
  background-color: grey;
  color: white;
}

.experienceFooter {
  display: flex;
  align-content: center;
  vertical-align: middle;
}
.TechWarper {
  width: auto;
  height: auto;
  margin: 0px 1vh 10px 1vh;

  background-color: blue;
  border: 1px solid blue;
  border-radius: 20px;
  letter-spacing: 2px;
  font: 1em "Margarine", sans-serif;
}
.TechWarper p {
  font-size: 1.2em;
  margin-left: 10px;
  margin-right: 10px;
}
.experienceTech {
  font-size: 1.2em;
}
